  width: 100%;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  align-items: stretch;
}

/*END PRICING CONTAINERS */
.professional-container {
  background: linear-gradient(hsl(236deg, 72%, 79%), hsl(237deg, 63%, 64%));
  gap: 1.3rem;
}

.professional-container > h1 {
  color: #fff;
}

.basic-container,
.master-container {
  background-color: white;
  gap: 1.3rem;
}

.pricing h2 {
  font-size: 1.23rem;
  font-weight: 700;
}

.container-2 h2 {
  padding-top: 12px;
}

.basic-container h2,
li,
.master-container h2,
li {
  color: hsl(234deg, 14%, 74%);
}

.pricing p {
  display: flex;
  align-items: center;
  gap: 13px;
  margin: 0 auto;
  font-size: 4.6rem;
  color: #fff;
}

.dollar-sign {
  font-size: 2.6rem;
}

.basic-container p,
.master-container p {
  color: hsl(232deg, 13%, 33%);
}

hr {
  width: 80%;
  border-width: 0.5px;
  margin: 0 auto;
  border-color: transparent;
  border-color: hsl(234deg, 14%, 74%);
  border-style: solid;
  opacity: 0.5;
}

.pricing ul {
  list-style: none;
  margin-top: 10px;
}

.pricing ul li {
  padding: 1.06rem;
}

.professional-container ul li {
  padding: 1.1rem;
  color: #fff;
}

.pricing button {
  cursor: pointer;
  width: 90%;
  margin: 0 auto;
  margin-top: 1rem;
  border-radius: 10px;
  border-color: transparent;
  border-style: solid;
  border-width: 1px;
  padding: 0.8rem;
  text-transform: uppercase;
  letter-spacing: 1.5px;
  word-spacing: 3px;
  font-weight: 700;
  text-align: center;
}

.professional-container button {
  color: hsl(237deg, 63%, 64%);
  background-color: white;
}

.basic-container button,
.master-container button {
  color: white;
  background: linear-gradient(hsl(236deg, 72%, 79%), hsl(237deg, 63%, 64%));
  background-repeat: no-repeat;
}

@media (hover: hover) {
  /* // hover: hover is not supported in IE 11 and below */
  .basic-container button:hover,
.master-container button:hover {
    background: transparent;
    border-color: hsl(234deg, 14%, 74%);
    color: var(--purple);
    border-width: 1px;
  }

  .professional-container button:hover {
    background-color: transparent;
    border-color: white;
    color: white;
    border-width: 1px;
  }
}
