/* Media Query */
@media screen and (max-width: 768px) {
  .pricing-container {
    width: 50%;
    grid-template-columns: 1fr;
    margin-top: 5.8rem;
    gap: 5rem;
  }

  .professional-container ul li {
    padding: 1.06rem;
  }
}
@media screen and (max-width: 768px) {
  .pricing-container {
    width: 60%;
  }
}
@media screen and (max-width: 320px) {
  .pricing-container {
    width: 90%;
  }
}